// tslint:disable-next-line:prefer-const
declare var BUILD_VERSION: any;
declare var BUILD_NUMBER: any;
import { Component } from "@angular/core";
import {
  AlertController,
  IonicPage,
  Loading,
  LoadingController,
  NavController,
  Platform,
  MenuController,
  Keyboard,
} from "ionic-angular";
import { AuthService } from "../../providers/auth.service";

@IonicPage()
@Component({
  selector: "page-login",
  templateUrl: "login.component.html",
})
export class LoginPage {
  private credentials = { username: "", password: "" };
  private loading: Loading;

  constructor(
    private nav: NavController,
    public auth: AuthService,
    private alertCtrl: AlertController,
    private platform: Platform,
    private menuCtrl: MenuController,
    public keyboard: Keyboard,
  ) { }

  public login() {
    this.auth.login(this.credentials).subscribe((allowed) => {
      if (allowed) {
        this.nav.setRoot("home");
      }
    });
  }

  isLargeScreen() {
    if ((this.platform.is("core") || this.platform.is("ipad") || this.platform.is("tablet"))
      && (this.platform.isLandscape())) {
      return true;
    }
    return false;
  }

  getAppVersion() {
    if (BUILD_NUMBER === "<BUILD_NUMBER>") {
            return BUILD_VERSION;
        } else {
            return BUILD_VERSION + "-" + BUILD_NUMBER;
        }
    }
}



