import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { HttpModule } from "@angular/http";

import { MyApp } from './app.component';
import { LoginPage } from '../pages/login/login.component';
import { HomeComponent } from '../pages/home/home.component';
import { RuleListComponent } from '../pages/rule-list/rule-list.component';
import { FormComponent } from '../pages/form/form.component';

import { LoginPageModule } from '../pages/login/login.component.module';
import { HomeComponentModule } from '../pages/home/home.component.module';
import { RuleListComponentModule } from '../pages/rule-list/rule-list.component.module';
import { FormComponentModule } from '../pages/form/form.component.module';

import { Session } from "../common/model/session";
import { HttpService } from "../common/utils/http.service";
import { ExceptionService } from "../providers/feedback/exception.service";
import { FeedbackService } from "../providers/feedback/feedback.service";

import { AuthService } from '../providers/auth.service';
import { FieldsService } from '../providers/fields.service';
import { OperatorsService } from '../providers/operators.service';
import { LogicalOperatorsService } from '../providers/logical.service';
import { RulesService } from '../providers/rules.service';
import { ResultsService } from '../providers/result.service';

import { RuleListService } from '../providers/ruleList.service';

@NgModule({
  declarations: [
    MyApp,
  ],
  imports: [
    BrowserModule,
    LoginPageModule,
    HomeComponentModule,
    RuleListComponentModule,
    FormComponentModule,
    HttpModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    LoginPage,
    HomeComponent,
    RuleListComponent,
    FormComponent
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    Session,
    HttpService,
    ExceptionService,
    FeedbackService,
    AuthService,
    FieldsService,
    OperatorsService,
    LogicalOperatorsService,
    RulesService,
    ResultsService,
    RuleListService,
  ]
})
export class AppModule {}



