import { Injectable } from "@angular/core";
import { Response } from "@angular/http";
import { Observable } from "rxjs/Rx";
import { FeedbackMessage } from "./feedback.messages";
import { FeedbackService } from "./feedback.service";

/**
 * Service class to handle all the exceptions
 */
@Injectable()
export class ExceptionService {

  constructor(private feedback: FeedbackService) {}

  /**
   * Converts the error response to string and logs it to the console
   * Shows the toast with the corresponding ErrorMessage
   */
  public handleErrorResponse(
    errorResponse: any,
    errorMessage: FeedbackMessage,
  ): Observable<any> {
    return this.handleErrorResponseAsToast(errorResponse, errorMessage);
  }

  /**
   * Converts the error response to string and logs it to the console
   * Shows the toast with the corresponding ErrorMessage
   */
  public handleErrorResponseAsToast(
    errorResponse: any,
    errorMessage: FeedbackMessage,
  ): Observable<any> {
    return Observable.of(errorResponse).map((data) => {
      const res = errorResponse as Response;
      const emsg = res ? JSON.stringify(res) : "unknown error";
      // console.debug("Error Occurred", emsg);
      this.feedback.showToast(errorMessage.toString());
    });
  }

  /**
   * Converts the error response to string and logs it to the console
   * Shows the toast with the corresponding ErrorMessage
   */
  public handleErrorResponseAsAlert(
    errorResponse: any,
    errorMessage: FeedbackMessage,
  ): Observable<any> {
    return Observable.of(errorResponse).map((data) => {
      const res = errorResponse as Response;
      const emsg = res ? JSON.stringify(res) : "unknown error";
      // console.debug("Error Occurred", emsg);
      this.feedback.showError(errorMessage.toString());
    });
  }

  /**
   * Converts the error response to string and logs it to the console
   * Shows the toast with the corresponding ErrorMessage
   * Saves the error response in the server side
   */
  public handleErrorResponseAndSave(
    errorResponse: any,
    errorMessage: FeedbackMessage,
  ): Observable<any> {
    this.log(errorResponse);
    return this.handleErrorResponseAsToast(errorResponse, errorMessage);
  }

  public log(error: any) {
    // Todo add logic to log the errors in the server side
  }
}



