import {
  Component,
  OnInit,
  ViewChild,
  ElementRef,
} from '@angular/core';
import {
  IonicPage,
  NavController,
  AlertController,
} from "ionic-angular";
import { RuleListService } from '../../providers/ruleList.service';
import { DatepickerOptions } from 'ng2-datepicker';
import { HomeComponent } from '../home/home.component';
import { FormComponent } from '../form/form.component';
import { Config } from "../../config/service-url.config";


@IonicPage({
  name: "rule-list",
  segment: "rule-list"
})
@Component({
  selector: 'rule-list',
  templateUrl: './rule-list.component.html'
})
export class RuleListComponent implements OnInit {

  public options: DatepickerOptions = {
    displayFormat: 'MM/DD/YYYY',
    barTitleFormat: 'MMMM YYYY',
  };

  @ViewChild("ruleListRows") ruleListRows: ElementRef;

  public localRuleList: any = [];
  private cachedRuleList: any = [];
  public showSearch: boolean = false;
  public selectedRules: any = [];

  public searchLastModDate: any = new Date();
  public searchLastPubDate: any = new Date();

  public homePage = HomeComponent;
  public formPage = FormComponent;

  constructor(public ruleListService: RuleListService, public navCtrl: NavController,
              public alertCtrl: AlertController) { }

  public ngOnInit() {
    // this.localRuleList = this.cachedRuleList = this.ruleListService.ruleList;
    this.ruleListService.getAllRules().subscribe((resp) => {
      this.localRuleList = this.cachedRuleList = resp;
      this.localRuleList.sort((a, b) => {
          let compA = a["rule_name"];
          let compB = b["rule_name"];

          if (compA < compB) {
            return -1;
          }
          else if (compA > compB) {
            return 1;
          }
          else {
            return 0;
          }
        });
    });
  }

  public formatDate(date) {
    if (date) {
      let localDate = new Date(date);
      return (localDate.getMonth() + 1) + '/' + localDate.getDate() + '/' + localDate.getFullYear();
    } else {
      return "-";
    }
  }

  public sortList(column, event) {
    let target = event.target || event.srcElement || event.currentTarget;
    let direction = target.className.split(" ")[1] == 'sort-down' ? -1 : 1;

    target.classList.toggle("sort-down");
    target.classList.toggle("sort-up");

    this.localRuleList.sort((a, b) => {
      let compA = a[column];
      let compB = b[column];

      if (column == "rule_category") {
        compA = a[column].name;
        compB = b[column].name;
      }

      if (compA < compB) {
        return -1 * direction;
      }
      else if (compA > compB) {
        return 1 * direction;
      }
      else {
        return 0;
      }
    });
  }

  public searchList(column, event) {
    let value = event.target.value;


    this.localRuleList = this.cachedRuleList.filter((elem) => {
      let toCheck = elem[column];

      if (column == "rule_category") {
        toCheck = elem[column].name;
      }

      if (toCheck.toLowerCase().indexOf(value.toLowerCase()) > -1) {
        return true;
      } else {
        return false;
      }
    });
  }

  public toggleCheckBox(event, rule) {
    let checked = event.target.checked;
    if (checked) {
      this.selectedRules.push(rule);
    } else {
      this.selectedRules = this.selectedRules.filter((elem) => {
        if (elem.rule_name == rule.rule_name) {
          return false;
        }
        return true;
      });
    }
    console.log(this.selectedRules);
  }

  public deleteRules() {
    let confirm = this.alertCtrl.create({
      title: 'Delete Rule(s)?',
      message: 'Are you sure you want to delete the selected rule(s) ?',
      buttons: [
        {
          text: 'Confirm',
          handler: () => {
            this.selectedRules.forEach((rule) => {
            this.ruleListService.deleteRuleSubscribe(rule.rule_name).subscribe((resp) => {
              console.log(resp);
              this.ngOnInit();
            });
            this.selectedRules = this.selectedRules.filter((elem) => {
                if (elem.rule_name == rule.rule_name) {
                  return false;
                }
                return true;
              });
            });
          }
        },
        {
          text: 'Cancel',
          handler: () => {
            // console.log('Operation Canceled');
          }
        }
      ]
    });
    confirm.present();
  }

  public cloneRules() {
    this.selectedRules.forEach((rule) => {
      this.ruleListService.cloneRule(rule).subscribe((res) => {
        console.log(res);
        
        this.ngOnInit();
        
        let ruleListRowElements = this.ruleListRows.nativeElement.querySelectorAll('input');
        ruleListRowElements.forEach((elem) => {
          if (elem.checked) {
            elem.click();
          }
        });

      });
    });
  }

  public tickAll(event) {
    let check = event.target.checked;
    let ruleListRowElements = this.ruleListRows.nativeElement.querySelectorAll('input');
    ruleListRowElements.forEach((elem) => {
      if (elem.checked != check) {
        elem.click();
      }
    })
  }

  public searchDate(column, date) {
    console.log('cal');
    this.localRuleList = this.cachedRuleList.filter((elem) => {
      let toCheckFullDate = new Date(elem[column]);
      let toCheckDate = toCheckFullDate.getDate();
      let toCheckMonth = toCheckFullDate.getMonth();
      let toCheckYear = toCheckFullDate.getFullYear();

      if (toCheckDate == date.getDate() && toCheckMonth == date.getMonth() && toCheckYear == date.getFullYear()) {
        return true;
      } else {
        return false;
      }
    });
  }

  public openRule(rule) {
    this.navCtrl.push("form", { 'rule-name': rule.rule_name });
  }

  public downloadUrl() {
    return Config.DOWNLOAD_XLS;
  }

}



