import { Injectable } from "@angular/core";
import { AlertController,
         LoadingController,
         ToastController } from "ionic-angular";

@Injectable()
export class FeedbackService {
  public static isLoaderVisible: boolean = false;
  constructor(
    private toastCtrl: ToastController,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
  ) {}

  public showToast(
    message: string,
    toastType: string = "info",
    duration: number = 2000,
    position: string = "bottom",
  ) {
    /*
     * toastType could be one of ['info', 'warning', 'error']
     * usage:
     *      this.feedbackService.showToast(
     *         <toast message>,
     *         <toastType>,
     *         <duration>,
     *         <position>,
     *      )
     */
    const toast =
      this.toastCtrl.create({ message, duration, position,
        cssClass: `toast__${toastType}`,
      });
    toast.present();
  }

  public getLoader(content: string = "loading ...") {
    // usage:
    // const loader = feedback.getLoader(<message>)
    // loader.present() => show loader with the message
    // loader.dismiss() => dismiss loader
    const loader = this.loadingCtrl.create({ content });
    return {
      dismiss: () => { loader.dismiss(); FeedbackService.isLoaderVisible = false; },
      present: () => { loader.present(); FeedbackService.isLoaderVisible = true; },
    };
  }

  public showError(text) {
    const alert = this.alertCtrl.create({
      buttons: ["OK"],
      subTitle: text,
      title: "Fail",
    });
    alert.present();
  }
}



