import { Injectable } from '@angular/core';
import data from "../assets/rule_list";
import { Config } from "../config/service-url.config";
import { HttpService } from "../common/utils/http.service";



@Injectable()

export class RuleListService {

	public ruleList: any = [];

	constructor(public http: HttpService) {
		console.log(data);
		this.ruleList = data;
	}

	public getAllRules() {
		return this.http.get(Config.RULE_LIST);
	}

	public getRule(name) {
		// return this.ruleList.filter((elem) => {
		// 	if (elem['rule_name'] == name) {
		// 		return true;
		// 	}
		// 	return false;
		// })[0];
		return this.http.get(Config.GET_RULE + "/" + name);
	}

	public updateRule(rule) {
		return this.http.put(Config.UPDATE_RULE + "/" + rule.name, rule);
	}

	public saveRuleSubscribe(rule) {
		return this.http.put(Config.CREATE_RULE, rule);
	}

	public saveRule(rule) {
		// this.ruleList.push(rule);
		this.saveRuleSubscribe(rule).subscribe((res) => {
			console.log(res);
		});
	}

	public deleteRuleSubscribe(name) {
		return this.http.delete(Config.DELETE_RULE + "/" + name);
	}

	public deleteRule(name) {
		// this.ruleList = this.ruleList.filter((elem) => {
		// 	if (elem['rule_name'] == name) {
		// 		return false;
		// 	}
		// 	return true;
		// });
		this.saveRuleSubscribe(name).subscribe((res) => {
			console.log(res);
		});
	}

	public cloneRule(rule) {
		let newRule  = JSON.parse(JSON.stringify(rule));
		newRule.rule_name = newRule.rule_name + " - Copy";
		newRule.last_published = "";
		newRule.last_modified = new Date();
		// this.ruleList.push(newRule);
		return this.saveRuleSubscribe(newRule);
	}

}



