/**
 * Class to define all the feedback message
 *
 * usage: FeedbackMessage.LOGIN_ERROR type is FeedbackMessage
 */

export enum FeedbackMessage {
    LOGIN_ERROR = "Invalid credentials , please try again" as any,
    INSERT_CREDENTIALS = "Please insert credentials" as any,
    NO_CHANGES = "No changes are made" as any,
    
}



