import { Injectable } from "@angular/core";
import {
  Headers,
  Http,
  RequestOptions,
  RequestOptionsArgs,
  Response,
  URLSearchParams,
  ResponseContentType,
} from "@angular/http";
import { Observable } from "rxjs/Observable";
import "rxjs/Rx";
import { Session } from "../model/session";
import { FeedbackService } from "../../providers/feedback/feedback.service";

/**
 * Wrapper for @angular/http
 */
@Injectable()
export class HttpService {
  constructor(private http: Http,
              private session: Session,
              public feedbackService: FeedbackService) {
              }

  /**
   * Performs a request with `get` http method.
   * @param url
   * @param params
   * @returns {Observable<any>}
   */
  public get(url: string, params?: any, headers?: any): Observable<any> {
    // let loader: any;
    // if (!FeedbackService.isLoaderVisible) {
    //   loader = this.feedbackService.getLoader();
    //   loader.present();
    // }
    params = this.addAccessTokenToParams(params);
    return this.intercept(this.http.get(url, this.getRequestOptions(params)));
  }

  /**
   * Performs a request with `Post` http method.
   * @param url
   * @param params
   * @returns {Observable<any>}
   */
  public post(url: string, body?: any, headers?: Headers, queryString?: string): Observable<any> {
    // let loader: any;
    // if (!FeedbackService.isLoaderVisible) {
    //   loader = this.feedbackService.getLoader();
    //   loader.present();
    // }
    let authUrl = this.addAccesstokenToUrl(url);
    if (queryString) {
      authUrl = authUrl + queryString;
    }
    // Create a request option
    const options = new RequestOptions({ headers });
    return this.intercept(this.http.post(authUrl, body, options));
  }

  /**
   * Performs a request with `Put` http method.
   * @param url
   * @param params
   * @returns {Observable<any>}
   */
  public put(url: string, body?: any, headers?: Headers, queryString?: string): Observable<any> {
    // let loader: any;
    // if (!FeedbackService.isLoaderVisible) {
    //   loader = this.feedbackService.getLoader();
    //   loader.present();
    // }
    let authUrl = this.addAccesstokenToUrl(url);
    if (queryString) {
      authUrl = authUrl + queryString;
    }
    // Create a request option
    const options = new RequestOptions({ headers });
    return this.intercept(this.http.put(authUrl, body, options));
  }

  /**
   * Performs a request with `Delete` http method.
   * @param url
   * @returns {Observable<any>}
   */
  public delete(url: string, headers?: Headers, queryString?: string): Observable<any> {
    // let loader: any;
    // if (!FeedbackService.isLoaderVisible) {
    //   loader = this.feedbackService.getLoader();
    //   loader.present();
    // }
    let authUrl = this.addAccesstokenToUrl(url);
    if (queryString) {
      authUrl = authUrl + queryString;
    }
    // Create a request option
    const options = new RequestOptions({ headers });
    return this.intercept(this.http.delete(authUrl, options));
  }

  public postFile(url: string, body?: any): Observable<any> {
    const authUrl = this.addAccesstokenToUrl(url);
    const opts = new RequestOptions({
      responseType: ResponseContentType.ArrayBuffer,
    });
    return this.http.post(authUrl, body, opts);
  }

  /**
   * To retrive accesstoken for the logged in user
   * @returns {String} accessToken
   */
  public getAccessToken(): string {
    if (this.session.get("currentUser")) {
      return this.session.get("currentUser").access_token;
    }
    return null;
  }

  /**
   * Converts the JSON object to query
   */
  public convertJsonToQueryString(params: any): URLSearchParams {
    const reqParams = new URLSearchParams();
    for (const key of Object.keys(params)) {
      reqParams.set(key, params[key]);
    }
    return reqParams;
  }

  private getRequestOptions(params: any, headers?: Headers): RequestOptions {
    const options = new RequestOptions();
    options.search = this.convertJsonToQueryString(params);
    options.headers = headers;
    return options;
  }

  private onCatch(error: any, caught: Observable<any>): Observable<any> {
    return Observable.throw(error);
  }

  private onSubscribeSuccess(response: Response): void {
    // Todo
  }

  private onSubscribeError(error: any): void {
    // Todo
  }

  private onFinally(): void {
    // Todo
  }

  private addAccesstokenToUrl(serviceURL: string): string {
    const accessToken = this.getAccessToken();
    // if (serviceURL && accessToken) {
    //   serviceURL += "?access_token=" + accessToken;
    // }
    return serviceURL;
  }

  private addAccessTokenToParams(data: any) {
    let computedData = {
      access_token: null,
    };
    if (data) {
      computedData = JSON.parse(JSON.stringify(data));
    }
    if (!computedData.access_token) {
      const accessToken = this.getAccessToken();
      if (accessToken) {
        computedData.access_token = accessToken;
      }
    }
    return computedData;
  }

  private intercept(observable: Observable<Response>): Observable<Response> {
    return observable.map((response) => response.json())
      .catch(this.onCatch)
      .do((res: Response) => {
        this.onSubscribeSuccess(res);
      },
      (error: any) => {
        this.onSubscribeError(error);
      })
      .finally(() => {
        // this.checkAndCloseLoader(loader);
        this.onFinally();
      });

  }

  private checkAndCloseLoader(loader: any) {
    if (loader) {
      loader.dismiss();
    }
  }
}
export const CONTENT_TYPE_FORM = "Content-Type: application/x-www-form-urlencoded";



